<template>
<div id="rentCar">
   <h2>商家如何录入车辆？</h2>
  <p>在工作台点击车辆录入，完成车辆录入操作，完成车辆录入后用户才可以取车。</p>
  <p>首先选择车辆要录入的商品，根据天天租/分时租选择不同的商品。第二步通过手动输入或扫码录入车辆SN，同一批SN录入同一个商品，可依次扫码录入。</p>
  <p>商品若录入错误，可能导致用户无法取车，需要在更改商品处进行更改。用户取车前请商家确认车辆已录入、车辆已经放入电池。</p>
  <div class="imgDiv">
    <img src="../../assets/m5.png" alt="">
  </div>

</div>
</template>

<script>
    export default {
        name: "rentCar"
    }
</script>

<style scoped lang="scss">

</style>



